import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { FooterPagesService } from '@services/modules/footer-pages/dataservice/footer-pages.service';
import { FooterPagesContent } from '@schemas/modules/footer-pages/footer-pages.model';
import { SettingsService } from '@services/services/settings-service';
import { UserService } from '@services/modules/user/services/user.service';

@Component({
    selector: 'tz-about',
    templateUrl: './about.component.html',
    styleUrls: ['./about.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class AboutComponent implements OnInit, OnDestroy {
    public content: FooterPagesContent | undefined;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        public footerPagesService: FooterPagesService,
        public userService: UserService,
        private settingsService: SettingsService
    ) {}

    ngOnInit() {
        this.settingsService.selectedLanguageSubject.pipe(takeUntil(this.destroy$)).subscribe(() => {
            this.getContent();
        });
    }

    public getContent(): void {
        this.footerPagesService.getPageContent('about').subscribe((content) => {
            this.content = content;
        });
    }

    public formSubmit(email: string): void {
        this.userService.subscribeToTrazler(email);
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
    }
}
